* {
  padding: 0;
  margin: 0;
}

body {
  text-align: center;
  font-family: 'Josefin Sans', 'Balsamiq Sans', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

h1 {
  margin-top: 20px;
}

.header {
  min-height: 6vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
  background-color: #7BA7AB;
  color: #fff1e6;
  font-weight: 700;
  font-size: 30px;
  padding: 20px;
}

.navigation_item {
  font-size: 17px;
  padding: 10px;
  color: #61dafb;
  display: inline-block;
}

.App-link {
  color: #61dafb;
}
